import React, { useState } from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

import { 
  Row, 
  Col, 
  ButtonToolbar, 
  DropdownButton,
  Dropdown
 } from "react-bootstrap"

import pose, { PoseGroup } from "react-pose"
import { tween } from "popmotion"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Container } from "react-bootstrap"
import { object } from "prop-types"

const imgStyle = {
  marginRight: "auto",
  marginLeft: "auto",
  display: "block",
}

const nameStyle = {
  textAlign: "center",
  marginTop: "0.5em",
  marginBottom: "0.5em",
  height: "2em",
}

const PoseCol = pose(Col)({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.2 },
  press: { scale: 1.1 },
  flip: {
    transition: tween,
  }
})

const Champions = () => {
  const data = useStaticQuery(graphql`
    {
      allChampionsJson(sort: {fields: name}) {
        edges {
          node {
            name
            fields {
              pathName
            }
            id
            stats {
              hp
              mp
              movespeed
              armor
              spellblock
              attackrange
              hpregen
              mpregen
              attackdamage
              attackspeed
            }
            thumbnailImg {
              childImageSharp {
                fixed(width: 120, quality: 100) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  `)

  const sleepMS = (ms) => {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  const ESortTypes = Object.freeze({
    AZ:     'Name',
    HP:     'HP',
    MP:     'MP',
    MVSPD:  'Move Speed',
    ARM:    'Armor',
    SBLK:   'Spell Block',
    ATKRNG: 'Attack Range',
    HPRGN:  'HP Regen',
    MPRGN:  'MP Regen',
    ATKDMG: 'Attack Damage',
    ATKSPD: 'Attack Speed',
  })

  const [sortCriteria, setSortCriteria] = useState(ESortTypes.AZ);
  const [sortAscending, setSortAscending] = useState(true);

  const organize = () => {
    // Champion frame item
    const layout = ( obj ) => {
      return(
        <PoseCol xs={6} md={3} lg={2} className="align-content-center champion-wrapper" style={{marginBottom: `1em`, paddingBottom: `2em`}} key={obj.node.id}>
          <Link to={obj.node.fields.pathName}>
            <Img 
              style={imgStyle}
              className="champion-icon"
              fixed={obj.node.thumbnailImg.childImageSharp.fixed}
              alt={obj.node.name}
            />
            <h3 style={nameStyle}>{obj.node.name}</h3>
            <p style={{textAlign: "center"}}>{additionalInfo(obj.node)}</p>
          </Link>
        </PoseCol>
      )
    }

    const additionalInfo = (node) => {
      switch (sortCriteria) {
        case ESortTypes.AZ:
          return('')
      
        case ESortTypes.HP:
          return(`HP: ${node.stats.hp}`)

        case ESortTypes.MP:
          return(`MP: ${node.stats.mp}`)
        
        case ESortTypes.MVSPD:
          return(`Move Speed: ${node.stats.movespeed}`)

        case ESortTypes.ARM:
          return(`Armor: ${node.stats.armor}`)

        case ESortTypes.SBLK:
          return(`Spell Block: ${node.stats.spellblock}`)

        case ESortTypes.ATKRNG:
          return(`Attack Range: ${node.stats.attackrange}`)

        case ESortTypes.HPRGN:
          return(`HP Regen: ${node.stats.hpregen}`)

        case ESortTypes.MPRGN:
          return(`MP Regen: ${node.stats.mpregen}`)

        case ESortTypes.ATKDMG:
          return(`Attack Damage: ${node.stats.attackdamage}`)

        case ESortTypes.ATKSPD:
          return(`Attack Speed: ${node.stats.attackspeed}`)

        default:
          break;
      }
    }

    // Copy from query
    let champions = [...data.allChampionsJson.edges];

    // Handle sort criteria
    switch (sortCriteria) {
      case ESortTypes.AZ:
        champions.sort((a, b) => (a.node.name < b.node.name ? -1 : a.node.name > b.node.name ? 1 : 0))
        break;
      
      case ESortTypes.HP:
        champions.sort((a, b) => (a.node.stats.hp < b.node.stats.hp ? -1 : a.node.stats.hp > b.node.stats.hp ? 1 : 0))
        break;

      case ESortTypes.MP:
        champions.sort((a, b) => (a.node.stats.mp < b.node.stats.mp ? -1 : a.node.stats.mp > b.node.stats.mp ? 1 : 0))
        break;

      case ESortTypes.MVSPD:
        champions.sort((a, b) => (a.node.stats.movespeed < b.node.stats.movespeed ? -1 : a.node.stats.movespeed > b.node.stats.movespeed ? 1 : 0))
        break;

      case ESortTypes.ARM:
        champions.sort((a, b) => (a.node.stats.armor < b.node.stats.armor ? -1 : a.node.stats.armor > b.node.stats.armor ? 1 : 0))
        break;

      case ESortTypes.SBLK:
        champions.sort((a, b) => (a.node.stats.spellblock < b.node.stats.spellblock ? -1 : a.node.stats.spellblock > b.node.stats.spellblock ? 1 : 0))
        break;

      case ESortTypes.ATKRNG:
        champions.sort((a, b) => (a.node.stats.attackrange < b.node.stats.attackrange ? -1 : a.node.stats.attackrange > b.node.stats.attackrange ? 1 : 0))
        break;

      case ESortTypes.HPRGN:
        champions.sort((a, b) => (a.node.stats.hpregen < b.node.stats.hpregen ? -1 : a.node.stats.hpregen > b.node.stats.hpregen ? 1 : 0))
        break;

      case ESortTypes.MPRGN:
        champions.sort((a, b) => (a.node.stats.mpregen < b.node.stats.mpregen ? -1 : a.node.stats.mpregen > b.node.stats.mpregen ? 1 : 0))
        break;

      case ESortTypes.ATKDMG:
        champions.sort((a, b) => (a.node.stats.attackdamage < b.node.stats.attackdamage ? -1 : a.node.stats.attackdamage > b.node.stats.attackdamage ? 1 : 0))
        break;

      case ESortTypes.ATKSPD:
        champions.sort((a, b) => (a.node.stats.attackspeed < b.node.stats.attackspeed ? -1 : a.node.stats.attackspeed > b.node.stats.attackspeed ? 1 : 0))
        break;
    
      default:
        break;
    }
    
    // Sort by direction
    let sortedChampions = [];
    if (sortAscending) {
      sortedChampions = [...champions];
    } else {
      sortedChampions = [...champions].reverse();
    }

    return(
      sortedChampions.map(layout)
    )

  }

  // const content = data.allChampionsJson.edges.map(( obj ) => (
  //   <Col xs={6} md={4} className="align-content-center">
  //     <Link to={obj.node.fields.pathName}>
  //       <h1 style={nameStyle}>{obj.node.name}</h1>
  //       <img style={imgStyle} src={`http://ddragon.leagueoflegends.com/cdn/9.22.1/img/champion/${obj.node.id}.png`} alt={`${obj.node.name}`}></img>
  //     </Link>
  //   </Col>
  // ))

  // Determine layout of sorting options
  const SortOptionsLayout = () => (
    Object.keys(ESortTypes).map((key) => (
      <Dropdown.Item
        active={sortCriteria === ESortTypes[key]}
        onSelect={() => {setSortCriteria(ESortTypes[key])}}>
        {ESortTypes[key]}
      </Dropdown.Item>
    ))
  )

  return (
    <Layout activeIndex='champions'>
      <SEO title={"Champions"}/>
      {/* <h1>Champions Page</h1> */}
      {/* <Button onClick={handleButtonClick}>{toggleButtonText}</Button> */}
      <Container style={{marginBottom: "2em"}}>
        <ButtonToolbar className="justify-content-end">
          <DropdownButton title={`Sort By: ${sortCriteria}, ${sortAscending ? 'Ascending' : 'Descending'}`} 
            variant="secondary" alignRight>
            <Dropdown.Header>Type</Dropdown.Header>
              {SortOptionsLayout()}
            <Dropdown.Divider></Dropdown.Divider>
            <Dropdown.Header>Order</Dropdown.Header>
              <Dropdown.Item 
                active={sortAscending} 
                onSelect={() => {setSortAscending(true)}}>
                Ascending
              </Dropdown.Item>
              <Dropdown.Item 
                active={!sortAscending} 
                onSelect={() => {setSortAscending(false)}}>
                Descending
              </Dropdown.Item>
          </DropdownButton>
        </ButtonToolbar>
      </Container>

      <Container>
        <Row>
          <PoseGroup>
            {organize()}
          </PoseGroup>
        </Row>
      </Container>
    </Layout>
  )
}

export default Champions